.chattingForm {
  display: flex;
  align-items: center;
  width: calc(100% + 20px); /*      */
  height: calc(100% + 20px); /*      */
  padding: 10px; /*   */
  background-color: var(--common-color);
  border-radius: 10px;
  // box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  margin: -10px; /*  padding   margin  */
}

.input {
  flex: 1; /*     */
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  background-color: var(--main-color);
}

.submitButton {
  padding: 10px 20px;
  background-color: var(--common-color);
  color: white;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  cursor: pointer;
  font-weight: bold;
  border: 1px solid var(--main-color);
  
  &:hover {
background-color: var(--hover-color);
  }
}
